import { Set } from "js-sdsl";
/** @internal */
export class MutIteratableCollection {
    constructor(comparator) {
        this._iterateCollection = null;
        this._collection = new Set(undefined, comparator);
        this._insertBuffer = new Set(undefined, comparator);
        this._insertBufferSwap = new Set(undefined, comparator);
        this._deleteBuffer = new Set(undefined, comparator);
    }
    get size() {
        return this._collection.size();
    }
    /** Set an entry, O(log n) */
    insert(value) {
        if (this._iterateCollection !== null)
            this._insertBuffer.insert(value);
        else
            this._collection.insert(value);
    }
    /** Delete an entry with the key from the tree, O(log n)
     *  Error: if the key is not in the tree */
    delete(value) {
        if (this._iterateCollection !== null) {
            value.isRemoved = true;
            this._deleteBuffer.insert(value);
        }
        else
            this._collection.eraseElementByValue(value);
    }
    /** Clear the tree, same as `Map.clear()`, O(1)
     * UB: you don't call this when iterating the tree */
    clear() {
        this._collection.clear();
        this._insertBuffer.clear();
        this._deleteBuffer.clear();
    }
    forEach(callback) {
        this._iterateCollection = this._collection;
        this._iterateCollection.forEach((value) => {
            if (!value.isRemoved)
                callback(value);
        });
        do {
            this._iterateCollection = this.flushBuffer();
            this._iterateCollection.forEach((value) => {
                if (!value.isRemoved)
                    callback(value);
            });
        } while (0 < this._insertBuffer.size() || 0 < this._deleteBuffer.size());
        this._iterateCollection = null;
    }
    flushBuffer() {
        this._iterateCollection = null;
        this._deleteBuffer.forEach((value) => this.delete(value));
        this._insertBuffer.forEach((value) => this.insert(value));
        const insertBuffer = this._insertBuffer;
        this._insertBuffer = this._insertBufferSwap;
        this._insertBufferSwap = insertBuffer;
        this._insertBuffer.clear();
        this._deleteBuffer.clear();
        return insertBuffer;
    }
}
